import React from "react";
import Card from "../Components/card";
import Type from "./typing";

const activities = [
    { time: "14:35 : ", content: "Fire alarm and smoke detectors activated." },
    {
      time: "14:38 : ",
      content: "Fire reported on RCT Level 35, evacuation initiated by SMC.",
    },
    {
      time: "14:40 : ",
      content: "2 firetrucks from Central Fire Station deployed.",
    },
    { time: "14:42 : ", content: "Central police station notified." },
    { time: "14:45 : ", content: "CCTV access authorized by SMC." },
    { time: "14:45 : ", content: "Observation drone deployed." },
    { time: "14:46 : ", content: "Firetrucks arrived on scene." },
  ];
  
export default function ActivityLog() {
    return (
      <Card>
        <div className="activity-log">
          <h1 className="content-title">Activity Log</h1>
          <div class="content-log">
            <div class="">
              <div>
                <Type text={activities} />
              </div>
            </div>
          </div>
        </div>
      </Card>
    );
  }