import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
} from "swiper";
// Import Swiper styles
import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

import WidgetValue from "./widget-value";
import { Row, Col } from "react-bootstrap";

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y, Autoplay]);

export default function Carousel() {
  return (
    <Row>
      <Col lg="2">
        <WidgetValue
          startvalue={2}
          duration={8}
          value={6}
          decimals={0}
          fontsize={"medium"}
          prefix={"High"}
          title={"Max UV Index"}
        />
      </Col>
      <Col lg="2">
        <WidgetValue
          value={6.3}
          prefix={"mm"}
          fontsize={"medium"}
          title={"Precipitation"}
          startvalue={3.12}
          duration={12}
          decimals={1}
        />
      </Col>
      <Col lg="2">
        <WidgetValue
          value={13}
          prefix={"km/h"}
          title={"Wind Gusts"}
          startvalue={19}
          duration={5}
          decimals={0}
          fontsize={"small"}
        />
      </Col>

      <Col lg="2">
        <WidgetValue
          value={56}
          prefix={"%"}
          title={"Cloud Cover"}
          startvalue={40}
          duration={10}
          decimals={0}
          fontsize={"small"}
        />
      </Col>

      <Col lg="2">
        <WidgetValue
          value={9}
          sufix={"SW"}
          prefix={"km/h"}
          title={"Wind"}
          startvalue={3}
          duration={12}
          decimals={0}
          fontsize={"small"}
        />
      </Col>

      <Col lg="2">
        <div className="stat-box">
          <div className="value small">GOOD</div>
          <div className="label">Air Quality</div>
        </div>
      </Col>
    </Row>
  );
}
