export function IncomingMapData() {
  let history = useHistory();

  function onClickedMarker() {
    history.push("/incident-report");
  }
  return (
    <div class="map">
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 3, duration: 1 }}
        class="fireMarker"
        onClick={() => onClickedMarker()}
      ></motion.div>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 1, duration: 1 }}
        class="vehicleIncidentMarker"
      ></motion.div>
    </div>
  );
}
