import React from "react";
import { Link } from "react-router-dom";
import Carousel from "./Carousel";
import Card from "./card";

function NEA_WeatherTracking() {
  return (
    <div class="social-media-tracking">
      <Card>
        <div className={"head-row"}>
          <h1 className="head">NEA Weather Tracking </h1>
          <div class="float-right">
            <button className={"button"}>BRANDWATCH</button>

            <Link to="/incident-report">
              <button className={"button"}>RAPID DEPLOY</button>
            </Link>
          </div>
        </div>

        <div class="inner-card carousel-row">
          <Carousel />
        </div>
      </Card>
    </div>
  );
}

export default NEA_WeatherTracking;
