import React from "react";
import Card from "../Components/card";
import Video from "./video";

export default function DroneCapture() {
    return <Card>
        <div className="drone-capture padding-bottom-25" >
            <h1 className="content-title">Drone Footage</h1>
            <div className="content-log">
                <div>
                    {/* <div className="drone-capture-img"></div> */}

                    <div className="video-container">
                        <Video
                            vid={"https://youtu.be/R0rNsLULlPI"}
                            className="video-container.fullscreen" />
                    </div>

                    <div className="drone-capture-content">
                        <div>
                            <p className="drone-capture-text">
                               
                            </p>
                        </div>
                        <div>
                            <ul className="drone-capture-1 float-left">
                                <li>Current Location</li>
                                <li className = "padding-top-5">Battery Level</li>
                            </ul>
                            <ul className="drone-capture-2 padding-left-50">
                                <li>450 North Bridge Rd.</li>
                                <li className = "drone-capture-battery-text padding-top-5">89%</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </Card>;
}