import React, { useState, useEffect } from "react";
import GaugeChart from "react-advanced-gauge-chart";

export function PSI() {
  const [count, setCount] = useState(0);
  const reading = [30, 150, 330];
  const band = ["Good", "Unhealthy", "Hazardous"];

  useEffect(() => {
    let id = setInterval(() => {
      setCount(count + 1);
    }, 5000);
    return () => clearInterval(id);
  });
  if (count === 3) {
    setCount(0);
  }
  let curReading = reading[count];
  let curBand = band[count];
  let colour = count + 1;

  return (
    <div>
      <p className="sensor-data-text">
        <span className="white-text">PSI LEVEL: {curReading}</span>
      </p>
      <p>
        <span className="white-text">Band: </span>
        <span className={"psi-band-text" + colour}>{curBand}</span>
      </p>
    </div>
  );
}

export function Gauge() {
  //   const timer = () => setCount(count - 1);
  //   const resetInterval = () => {
  //     setCount(0);
  //   };
  //   useInterval(() => {
  //     // Your custom logic here
  //     setCount(count + 1);
  //   }, 1000);

  const [count, setCount] = useState(0);
  const reading = [50, 72, 84, 89, 58, 64];

  useEffect(() => {
    let id = setInterval(() => {
      setCount(count + 1);
    }, 1000);
    return () => clearInterval(id);
  });
  if (count === 3) {
    setCount(0);
  }
  let val = reading[count];
  return (
    <div>
      <GaugeChart
        // style = "height:100px;"
        id="gauge-chart4"
        nrOfLevels={4}
        arcPadding={0.00001}
        arcsLength={[0.183, 0.317, 0.33, 0.17]}
        arcWidth={0.1}
        colors={["#96C3EB", "#4073FF", "#884DFF", "#E05194"]}
        percent={val / 300}
        formatTextValue={(percent) => percent * 3 + "g/m3"}
        animate={true}
        textColor={"#FFFFFF"}
        needleColor={"#DB4035"}
        style={{ width: 180 }}
      />
    </div>
  );
}

export default function SensorData() {
  const [count, setCount] = useState(0);
  const psiReading = [20,70 , 150, 210, 230, 290];
  const band = [
    "Good", "Moderate","Unhealthy", "Very unhealthy" ,"Very unhealthy","Hazardous"
  ];
  const pmReading = [51, 72, 84, 87, 57, 63];
  const colorText = [1,4,2,2,2,3]
  useEffect(() => {
    let id = setInterval(() => {
      setCount(count + 1);
    }, 1500);
    return () => clearInterval(id);
  });
  if (count === 6) {
    setCount(0);
  }
  let curPsiReading = psiReading[count];
  let curBand = band[count];
  // let colour = count + 1;
  let val = pmReading[count];

  return (
    <div className="content-log">
      <div className="sensor-data-left" style={{ paddingTop: "10px" }}>
        <div>
          <p className="sensor-data-text">AIR QUALITY</p>
        </div>
        <div>
          <p className="sensor-data-text">
            <span className="white-text">PSI LEVEL: {curPsiReading}</span>
          </p>
          <p>
            <span className="white-text">Band: </span>
            <span className={"psi-band-text" + colorText[count]}>{curBand}</span>
          </p>
        </div>
      </div>
      <div className="sensor-data-middle" style={{ paddingTop: "10px" }}>
        <div>
          <GaugeChart
            // style = "height:100px;"
            id="gauge-chart4"
            nrOfLevels={4}
            arcPadding={0.00001}
            arcsLength={[0.183, 0.317, 0.33, 0.17]}
            arcWidth={0.1}
            colors={["#00ff00", "#FFDC00", "#ff964f","#ff0000"]}
            percent={val / 300}
            formatTextValue={(percent) => percent * 3 + "g/m3"}
            animate={true}
            textColor={"#FFFFFF"}
            needleColor={"#DB4035"}
            style={{ width: 180 }}
          />
        </div>
      </div>
      <div className="sensor-data-right" style={{ paddingTop: "10px" }}>
        <div>
          <p className="sensor-data-right-text">ELEVATED</p>
        </div>
        <div>
          <p className="sensor-data-text white-text">1 HR PM 2.5</p>
          <p className="sensor-data-text white-text">reading g/m3</p>
        </div>
      </div>
    </div>
  );
}
