import React, { useState } from "react";
import { Col, Row, Button } from "react-bootstrap";
import Alert from "react-bootstrap/Alert";
import Video from "./video";
import Card from "./card";

export default function RespondersMap(props) {
  const [showResponder, setShowResponder] = useState(false);
  const [showEMS, setShowEMS] = useState(false);
  const [map, setMap] = useState(0);
  const [patriciaAcceptPin, setRemovePatriciaAcceptPin] = useState(false);
  const [williamName, setRemoveWilliamName] = useState(false);

  const greyPins = <div className="map-pins-grey" />;
  const defaultPins = <div className="map-pins" />;
  const yellowPinsPartial = <div className="map-pins-yellow-partial" />;
  const yellowPinsFull = <div className="map-pins-yellow-full" />;
  const pinsFinish = <div className="map-pins-finish" />;

  const triggerResponderArrival = () => {
    if (props.enableClickMapForArrival) {
      props.triggerResponderArrival("RESPONDER-ARRIVED");
    }
  };

  if (props.enableResponder) {
    if (map === 0) {
      setMap(1);
    }
  }

  if (props.williamAccept) {
    if (map === 1) {
      setMap(2);
    }
  }

  if (props.williamAccept && props.patriciaAccept && props.amirulAccept) {
    if (map === 2) {
      setMap(3);
      setRemovePatriciaAcceptPin(true);
      setRemoveWilliamName(true);
    }
  }

  const stopTimer = (timer) => {
    console.log("timer stoped");
    clearTimeout(timer);
    //setStopTime(true);
  };

  const startTimer = () => {
    const timer = setTimeout(() => {
      setShowResponder(false);
      setShowEMS(true);
      stopTimer(timer);
    }, 3000);
    // stopTimer(timer);
  };
  const EMSArrived = () => {
    setShowEMS(false);
    props.EMSArrived(true);
  };

  if (props.enableResponderArrival) {
    if (map === 3) {
      setMap(4);
      setShowResponder(true);
      startTimer();
    }
  }

  return (
    <div
      className={"respondersMapBox"}
      onClick={() => triggerResponderArrival()}
    >
      <div className="responders-legend" />
      {map === 0 && defaultPins}
      {map === 1 && greyPins}
      {map === 2 && yellowPinsPartial}
      {map === 3 && yellowPinsFull}
      {map === 4 && pinsFinish}
      {props.williamAccept === true && williamName === false && (
        <div className="william-ng-name" />
      )}
      {props.patriciaAccept === true && patriciaAcceptPin === false && (
        <div>
          <div className="patricia-lim-name" />
          <div className="patricia-lim-yellow-pin" />
        </div>
      )}

      <div className="circle-overlay" />
      <div className="base-map" />

      {showResponder === true && (
        <div className={"alert-container"}>
          <div className={"alert-icon"}></div>
          <div>
            <div className={"title"}>
              Responder William Ng has arrived at the scene
            </div>
            <div className={"label"}>CPR/AED Trained</div>
          </div>
          <div
            className={"close-popup btn btn-primary"}
            onClick={() => setShowResponder(false)}
          >
            OKAY
          </div>
        </div>
      )}

      {showEMS === true && (
        <div className={"alert-container"}>
          <div className={"popup-amb-icon"}></div>
          <div>
            <div className={"title"}>Emergency Medical Service has arrived</div>
            <div className={"label"}>Plate no: QX 858P</div>
          </div>
          <div className={"close-popup btn btn-primary"} onClick={EMSArrived}>
            OKAY
          </div>
        </div>
      )}
    </div>
  );
}
