import React from "react";
import { Row, Col, Button } from "react-bootstrap";

const SelectRate = (props) => {
  return (
    <div className={"select-rate margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Select severity rating:</div>
        </Col>
        <Col lg={9}>
          <div className={"row incident-button"}>
            <Button
              value="Life-threatening"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton === "Life-threatening"
                  ? "btn-rate-active btn-danger"
                  : "btn-rate btn-danger"
              }
              onClick={props.handleChange}
            >
              Life-threatening
            </Button>
            <Button
              value="Emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton === "Emergency"
                  ? "btn-rate-active btn-warning"
                  : "btn-rate btn-warning"
              }
              onClick={props.handleChange}
            >
              Emergency
            </Button>
            <Button
              value="Minor emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton === "Minor emergency"
                  ? "btn-rate-active btn-info"
                  : "btn-rate btn-info"
              }
              onClick={props.handleChange}
            >
              Minor emergency
            </Button>
            <Button
              value="Non-emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton === "Non-emergency"
                  ? "btn-rate-active btn-success"
                  : "btn-rate btn-success"
              }
              onClick={props.handleChange}
            >
              Non-emergency
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SelectRate;
