import Card from "./card";
import SensorData from "./GaugeChart";
import React from "react";

export function SensorDataCard() {
    return <Card>
        <div className="sensor-data">
            <h1 className="content-title">Sensor Data</h1>
            <SensorData/>
        </div>
        <div className = "sensor-card-height"/>
    </Card>;
}
