import React, { useState, useEffect } from "react";
import Clock from "react-live-clock";

function Header(props) {
  const titleArray = [
    "HEAVY RAINFALL NEAR THE SERANGOON AREA",
    "CAR CRASH AT PAN ISLAND EXPRESSWAY EXIT 17D",
    "FIRE BREAKOUT AT RAFFLES CITY TOWER",
    "MULTIPLE FLASH FLOODS OCCURING AROUND THE ISLAND",
  ];
  const [count, setCount] = useState(0);

  useEffect(() => {
    let interval = null;
    if (props.clicked) {
      interval = setInterval(() => {
        setCount((prevTime) => prevTime + 1);
      }, 10000);
    } else {
      clearInterval(interval);
    }

    return () => clearInterval(interval);
  }, [props.clicked]);

  if (count === 4) {
    setCount(0);
  }

  let titleReading = titleArray[count];

  return (
    <div class="header-container">
      <div className={"header"}>
        <div className={"h-title"}>{titleReading}</div>
        <div className={"menu-liner"}>
          <span className={"ml-text label"}> 24hr Weather Forecast </span>{" "}
          <span className={"ml-text value"}> THUNDERSTORM </span>
          <span className={"ml-text label"}>Temperature </span>{" "}
          <span className={"ml-text value"}>25-33C</span>
          <span className={"ml-text label"}>UV Index</span>{" "}
          <span className={"ml-text value"}>2 - LOW</span>
          <span className={"ml-text label"}>Rain Intensity</span>{" "}
          <span className={"ml-text warning"}>MODERATE</span>
          <span className={"ml-text label"}>Precipitation</span>{" "}
          <span className={"ml-text value"}>90%</span>
          <span className={"ml-text label"}>Wind</span>{" "}
          <span className={"ml-text value"}>8-10 KM/H</span>
        </div>
      </div>

      <Clock
        className="headerClock"
        format={"ddd HH:mm MMM DD, YYYY "}
        ticking={true}
        timezone={"Asia/Singapore"}
      />
    </div>
  );
}

export default Header;
