import React, { useState, useRef } from "react";
import { Col, Row } from "react-bootstrap";
import Card from "../Components/card";
import CitizenRespondersHeader from "../Components/CitizenRespondersHeader";
import RespondersMap from "../Components/RespondersMapCard";
import CitizenWatchCallCard from "../Components/CitizenWatchCallCard";
import CitizenOverview from "../Components/CitizenOverview";
import Constants from "../Constants";
import ResponderNearby from "../Components/ResponderNearby";
import AssetManagement from "../Components/AssetManagement";
import ActivityLogCW from "../Components/cs-activitylog";
import { CwActivityLogState } from "../Atom";
import { useRecoilState } from "recoil";
function CitizenWatch() {
  const ActivityLogRef = useRef(null);
  const [enableResponder, setEnableResponder] = useState(false);
  const [enableIconBlink, setEnableIconBlink] = useState(false);
  const [enableResponderButton, setEnableResponderButton] = useState(false);
  const [enableCRP, setEnableCRP] = useState(false);
  const [assetContent, setAssetContent] = useState(false);
  const [caller, setCaller] = useState(false);
  const [videoCall, setVideoCall] = useState(false);
  const [williamAccept, setWilliamAccept] = useState(false);
  const [patriciaAccept, setPatriciaAccept] = useState(false);
  const [amirulAccept, setAmirulAccept] = useState(false);
  const [enableResponderArrival, setEnableResponderArrival] = useState(false);
  const [enableResponderArrived, setEnableResponderArrived] = useState(false);
  const [emsArrived, setEMSArrived] = useState(false);
  const [assetManagementHeight, setAssetManagementHeight] = useState(
    "assetManagementMap-content inner-card"
  );
  const [activityState, setActivityState] = useRecoilState(CwActivityLogState);

  const [startCall, setStartCall] = useState(false);

  const [enableClickMapForArrival, setEnableClickMapForArrival] =
    useState(false);

  const doCallBack = (value) => {
    if (value === Constants.caseType.CADRIACARREST) {
      setEnableResponder(true);
    }
    if (value === Constants.severityType.LIFETHREATENING) {
      setEnableIconBlink(true);
      setEnableCRP(true);
    }
    if (value === "RequestForEMS-Submmited") {
      setEnableResponderButton(true);
    }
  };

  const sendCall = (value) => {
    if (value === "clickMapForArrival") {
      setEnableClickMapForArrival(true);
    }
    if (value === "williamAccept") {
      setWilliamAccept(true);
    }
    if (value === "patriciaAccept") {
      setPatriciaAccept(true);
    }
    if (value === "amirulAccept") {
      setAmirulAccept(true);
    }
  };

  const triggerResponderArrival = (value) => {
    if (value === "RESPONDER-ARRIVED") {
      setEnableResponderArrival(true);
    }
  };

  const assetMap = (value) => {
    setAssetContent(value);
  };

  const endCall = (value) => {
    setCaller(value);
  };

  const onVideocall = (value) => {
    setVideoCall(value);
  };

  const callAssetManagement = (value) => {
    if (value === "requestClicked") {
      setAssetManagementHeight(
        "assetManagementMap-content-secondary inner-card"
      );
    }
  };

  const EMSArrived = (value) => {
    setEnableResponderArrived(value);
  };

  const CPRRequest = (value) => {
    setActivityState(value);
  };

  return (
    <div className={"citizen-watch-container"}>
      <CitizenRespondersHeader />

      <div className="container-dashboard">
        <Row>
          <Col lg={3}>
            <ResponderNearby
              enableResponder={enableResponder}
              enableIconBlink={enableIconBlink}
              enableResponderButton={enableResponderButton}
              enableCRP={enableCRP}
              enableResponderArrival={enableResponderArrival}
              sendCall={sendCall}
              callAssetManagement={callAssetManagement}
              CPRRequest={CPRRequest}
            />
            <div className="padding-top-20"></div>
            <AssetManagement
              assetContent={assetContent}
              enableResponder={enableResponder}
              enableResponderArrived={enableResponderArrived}
              assetManagementHeight={assetManagementHeight}
            />
          </Col>
          <Col lg={6}>
            <RespondersMap
              enableResponderArrival={enableResponderArrival}
              triggerResponderArrival={triggerResponderArrival}
              enableClickMapForArrival={enableClickMapForArrival}
              enableResponder={enableResponder}
              williamAccept={williamAccept}
              patriciaAccept={patriciaAccept}
              amirulAccept={amirulAccept}
              EMSArrived={EMSArrived}
            />

            <div className="padding-top-20"></div>
            <CitizenOverview
              doCallBack={doCallBack}
              assetMap={assetMap}
              endCall={endCall}
            />
          </Col>
          <Col lg={3}>
            <CitizenWatchCallCard
              onStartCall={() => {
                console.log("on start call");
                setStartCall(true);
              }}
              Screen={Constants.callState[0]}
              endCall={caller}
              onVideocall={onVideocall}
            />
            <div className="padding-top-20"></div>

            {startCall && (
              <ActivityLogCW
                onEvent={(event) => {
                  console.log("onEvent", event);
                }}
                videoCall={videoCall}
                //crpRequest={crpRequest}
              />
            )}
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default CitizenWatch;
